.quote {
  padding-top: 25px;
  font-size: 25px;
  color: rgba(248, 241, 241, 0.932);
}

.designation {
  color: white;
}

.BannerDiv {
  max-width: 100%;
  max-height: 100%;
  display: contents;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

@media screen and (max-width: 400px) {
  .Banner {
    height: 130vw;
  }

  .body {
    text-align: center;
  }
}

@media screen and (max-width: 500px) and (min-width: 401px) {
  .Banner {
    height: 100vw;
  }

  .body {
    text-align: center;
  }
}

@media screen and (min-width: 501px) {
  .Banner {
    height: 40vw;
  }

  .body {
    text-align: center;
  }
}

.BannerDiv h1 {
  position: relative;
  top: 25%;
  transform: translateY(-25%);
}

.BannerDiv p {
  position: relative;
  top: 25%;
  transform: translateY(-50%);
}
