.profile-pic {
  height: 6vmin;
  margin-right: 10px;
  border-radius: 50px;
}

.name {
  font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  color: whitesmoke;
  font-weight: 600;
  text-align: left;
  padding-left: 10%;
  font-size: 1.55rem;
}

@media screen and (max-width: 500px) {
  .name {
    text-align: center;
    padding-left: 0%;
    font-size: 1rem;
    font-family: apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol";
    color: whitesmoke;
    font-weight: 600;
  }
}
